  function addAsFirstSection(pElement, pDefaultValues) {
    const title = pElement === "Document.TableOfContents" ? "Table Of Contents" : undefined
    let newSection = getNewSection(title)
    newSection.Elements.push(pDefaultValues)

    setJson({ Sections: [newSection, ...json.Sections] })
    setCurrentSectionIndex(0)

    // let currentSection = json.Sections[0]
  }

  // TODO - REMOVE IDs ON THE DUPLICATED SECTION CONTROLS -----------------------------------------------------//
  function duplicateSection() {

    if (json.Sections[currentSectionIndex] !== undefined) {
      // Deep copy
      let sectionToDuplicate = JSON.parse(JSON.stringify(json.Sections[currentSectionIndex]))

      sectionToDuplicate.SectionId = getUniqID(5)
      sectionToDuplicate.Title = `${json.Sections[currentSectionIndex].Title}-copy`
      sectionToDuplicate.Elements.forEach(item => {
        if (item.ElementId === undefined || item.ElementId.length > 0) item.ElementId = getUniqID(11)
        if (item.Id !== undefined) item.Id = `${item.Id}-copy`
        if (item.Elements) {
          item.Elements.forEach(subitem => {
            if (subitem.ElementId === undefined || subitem.ElementId.length > 0) subitem.ElementId = getUniqID(11)
            if (subitem.Id !== undefined) subitem.Id = `${subitem.Id}-copy`
            if (subitem.Elements) {
              subitem.Elements.forEach(subsubitem => {
                if (subsubitem.Id !== undefined) subsubitem.Id = `${subsubitem.Id}-copy`
                if (subsubitem.ElementId !== undefined) subsubitem.ElementId = getUniqID(11)
              })
            }

          })
        }
      })

      setJson({ Sections: [...json.Sections.slice(0, currentSectionIndex + 1), sectionToDuplicate, ...json.Sections.slice(currentSectionIndex + 1)] })
      setCurrentSectionIndex(currentSectionIndex + 1)
    }
  }

  function removeSection() {
    let sections = json.Sections.filter((section, index) => {
      if (index !== currentSectionIndex) {
        return true
      }
      else {
        unregisterSectionControls(json.Sections[index])
        return false
      }
    })

    setCurrentSectionIndex(json.Sections.length - 2)
    setJson({ Sections: [...sections] })
  }

  function moveSectionLeft() {
    if (currentSectionIndex > 0) {
      const reorderedSections = json.Sections
      reorderedSections.splice(currentSectionIndex, 0, reorderedSections.splice(currentSectionIndex - 1, 1)[0])
      setCurrentSectionIndex(currentSectionIndex - 1)
      setJson({ Sections: [...reorderedSections] })
    }
  }

  function moveSectionRight() {
    if (currentSectionIndex + 1 < json.Sections.length) {
      const reorderedSections = json.Sections
      reorderedSections.splice(currentSectionIndex, 0, reorderedSections.splice(currentSectionIndex + 1, 1)[0])
      setCurrentSectionIndex(currentSectionIndex + 1)
      setJson({ Sections: [...reorderedSections] })
    }
  }

  /**-------------------------------------------------**
  * @desc - Utilities                             -----*
  *----------------------------------------------------*/

  function getNewSection(pTitle) {
    const title = renameSectionTitle(pTitle)
    const sectionTitle = title === undefined ? `Section ${json.Sections.length + 1 || 1}` : title
    return (
      {
        SectionId: getUniqID(5),
        Title: sectionTitle,
        Elements: []
      }
    )
  }

  function getNewElement(pElement, pIsMatrix, pIsMatrixButton, pIsRepeatable, pChildren) {
    // Repeatable children length are not known
    const matrixElementsInit = [{
      Type: "GridRow",
      // ToDo: generate unique Id
      ElementId: getUniqID(),
      Elements: [
        {}
      ]
    }]

    if (pIsMatrix) {
      return (
        {
          Type: pElement,
          Id: getDefaultID(pElement),
          ElementId: getUniqID(11),
          Columns: "1",
          Rows: "1",
          Elements: matrixElementsInit
        }
      )
    }
    if (pIsMatrixButton) {
      return (
        {
          Type: pElement,
          Id: getDefaultID(pElement),
          ElementId: getUniqID(11),
          Columns: "1",
          Rows: "1",
          Elements: matrixElementsInit,
          Title: "",
          Selection: "single",
          XAxisName: "",
          YAxisName: "",
          Legend: { ShowLegend: false, LegendValuesX: { Name: '', Values: [] }, LegendValuesY: { Name: '', Values: [] } }
        }
      )
    }
    if (pIsRepeatable) {
      return (
        {
          Type: pElement,
          Id: getDefaultID(pElement),
          ElementId: getUniqID(11),
          Elements: []
        }
      )
    }
    else {
      if (pChildren) {
        return (
          {
            Type: pElement,
            ElementId: getUniqID(11),
            Elements: pChildren
          }
        )
      }
      else if (pElement.includes('Button')) {
        if (pElement === "Button.Add.DocketNumber")
          return (
            {
              Type: pElement,
              Id: 'Button.Add.DocketNumber',
              ElementId: getUniqID(11),
              Title: "Delivery Docket Number",
            }
          )
        else return (
          {
            Type: pElement,
            ElementId: getUniqID(11)
          }
        )
      }
      else {
        return (
          {
            Type: pElement,
            Id: getDefaultID(pElement),
            ElementId: getUniqID(11),
          }
        )
      }
    }
  }

  function getElementFromElementId(pElementID) {
    const currentSection = json.Sections[currentSectionIndex]
    let foundItem = {}

    currentSection.Elements.forEach(item => {
      if (item.hasOwnProperty('Elements')) {

        item.Elements.forEach(subitem => {
          if (subitem.hasOwnProperty('Elements')) {

            subitem.Elements.forEach(subsubitem => {
              if (subsubitem.ElementId === pElementID) {
                // console.log('---> Trouv subsubitem: ', subsubitem)
                foundItem = subsubitem
              }
            })

          }

          if (subitem.ElementId === pElementID) {
            // console.log('---> Trouv subitem: ', subitem)
            foundItem = subitem
          }

        })
      }

      if (item.ElementId === pElementID) {
        // console.log('---> Trouv item: ', item)
        foundItem = item
      }

    })
    return foundItem
  }

  function getElementsFromId(pID) {
    let foundItem = []
    json.Sections.forEach(section => {
      section.Elements.forEach(item => {
        if (item.hasOwnProperty('Elements')) {

          item.Elements.forEach(subitem => {
            if (subitem.hasOwnProperty('Elements')) {

              subitem.Elements.forEach(subsubitem => {
                if (subsubitem.Id === pID) {
                  // console.log('---> Trouv subsubitem: ', subsubitem)
                  foundItem.push(subsubitem)
                }
              })

            }

            if (subitem.Id === pID) {
              // console.log('---> Trouv subitem: ', subitem)
              foundItem.push(subitem)
            }

          })
        }

        if (item.Id === pID) {
          // console.log('---> Trouv item: ', item)
          foundItem.push(item)
        }

      })
    })

    return foundItem
  }

  function getElementsFromType(pType) {
    let foundItem = []
    json.Sections.forEach(section => {
      section.Elements.forEach(item => {
        if (item.hasOwnProperty('Elements')) {

          item.Elements.forEach(subitem => {
            if (subitem.hasOwnProperty('Elements')) {

              subitem.Elements.forEach(subsubitem => {
                if (subsubitem.Type === pType) {
                  // console.log('---> Trouv subsubitem: ', subsubitem)
                  foundItem.push(subsubitem)
                }
              })

            }

            if (subitem.Type === pType) {
              // console.log('---> Trouv subitem: ', subitem)
              foundItem.push(subitem)
            }

          })
        }

        if (item.Type === pType) {
          // console.log('---> Trouv item: ', item)
          foundItem.push(item)
        }

      })
    })

    return foundItem
  }

  function getCurrentSection() {
    return json.Sections[currentSectionIndex]
  }

  function deleteElementFromId(pElementID) {
    unregisterControlId(pElementID)

    unregisterControlChildrenId(pElementID)

    // console.log('remove this guy: ', getElementFromElementId(pElementID))

    let jsonToUpdate = { ...json }
    setSelectedControl({ ElementId: 0 })

    const currentSection = jsonToUpdate.Sections[currentSectionIndex]

    currentSection.Elements.forEach((item, index) => {
      if (item.hasOwnProperty('Elements')) {

        item.Elements.forEach((subitem, subindex) => {
          if (subitem.hasOwnProperty('Elements')) {

            subitem.Elements.forEach((subsubitem, subsubindex) => {
              if (subsubitem.ElementId === pElementID) {
                // console.log(`--->: deleteElementFromId -> subsubitem`, subsubitem)
                let elementsToFilter = [...subitem.Elements]
                const elementsIndex = elementsToFilter.findIndex(subsubitem => subsubitem.ElementId === pElementID)
                elementsToFilter[elementsIndex] = {}
                subitem.Elements = elementsToFilter
              }
            })

          }

          if (subitem.ElementId === pElementID) {
            let elementsToFilter = [...item.Elements]
            // console.log(`--->: deleteElementFromId -> subitem`, subitem)

            elementsToFilter = elementsToFilter.filter(subitem => subitem.ElementId !== pElementID)
            item.Elements = elementsToFilter
          }

        })
      }

      if (item.ElementId === pElementID) {
        const newElements = currentSection.Elements.filter(item => item.ElementId !== pElementID)
        currentSection.Elements = newElements
      }

    })

    setJson(jsonToUpdate)

  }

  function getParentElementFromId(pElementID) {
    const currentSection = json.Sections[currentSectionIndex]
    let foundItem = {}

    currentSection.Elements.forEach(item => {
      if (item.hasOwnProperty('Elements')) {

        item.Elements.forEach(subitem => {
          if (subitem.hasOwnProperty('Elements')) {

            subitem.Elements.forEach(subsubitem => {
              if (subsubitem.ElementId === pElementID) {
                // console.log('---> Trouv subsubitem: ', subsubitem)
                foundItem = subitem
              }
            })

          }

          if (subitem.ElementId === pElementID) {
            // console.log('---> Trouv subitem: ', subitem)
            foundItem = item
          }

        })
      }

      if (item.ElementId === pElementID) {
        // console.log('---> Trouv item: ', item)
        foundItem = currentSection.Elements
      }

    })
    return foundItem
  }

  function validateId(pID) {
    if (getElementsFromId(pID).length > 0) setIsValidId(false)
    else setIsValidId(true)
  }

  function initButtonsProperty(pElement) {
    const buttons = [{
      Button: "Yes",
      Label: "Yes",
      BackgroundColor: "#D3D3D3",
      BackgroundColorSelected: "#66ff33"
    }, {
      Button: "No",
      Label: "No",
      BackgroundColor: "#D3D3D3",
      BackgroundColorSelected: "#d63a3a"
    }, {
      Button: "NA",
      Label: "Not Applicable",
      BackgroundColor: "#D3D3D3",
      BackgroundColorSelected: "#808080"
    }]
    pElement.Buttons = buttons
  }

  function initFormatProperty(pElement) {
    pElement.Format = {}
  }
  function initDefaultValues(pElement) {
    if (pElement.Type === "Trilean.Label") {
      pElement.Title = "comment"
      pElement.CommentValue = "comment written here"
    }
  }

  function registerControlId(pControl) {
    // console.log('registerControlId: --> [', pControl.Id, '(new value: ', pValue, ')]')

    if (idsCollection[pControl.Type] === undefined) {
      idsCollection[pControl.Type] = { collection: [] }
    }

    const idIndex = idsCollection[pControl.Type].collection.findIndex(item => item.ElementId === pControl.ElementId)

    let idObject = { ElementId: pControl.ElementId, Id: pControl.Id }
    if (idIndex > -1) {
      // console.log('ID exists at pos: ', idIndex)
      idsCollection[pControl.Type].collection[idIndex] = idObject
    }
    else idsCollection[pControl.Type].collection.push(idObject)
    // console.log(`--->: registerControlId -> idsCollection: `, idsCollection)
  }

  function unregisterControlId(pElementId) {
    // console.log(`--->: unregisterControlId: `, pElementId, ' inside: ', idsCollection)

    let foundItem
    for (const type in idsCollection) {
      idsCollection[type].collection.forEach(item => {
        if (item.ElementId === pElementId) foundItem = item.Id
      })

      idsCollection[type].collection = idsCollection[type].collection.filter(item => item.ElementId !== pElementId)
    }
    // console.log('foundItem: ', foundItem)
    idsLoadedList.delete(foundItem)

    // console.log(`--->: unregisterControlId -> idsCollection[type].collection`, idsCollection[type].collection)
  }

  function unregisterControlChildrenId(pElementId) {
    const element = getElementFromElementId(pElementId)

    if (element.hasOwnProperty('Elements')) {

      element.Elements.forEach(item => {
        if (item.Id !== undefined) {
          console.log('unregister child: ', item.Id)
          unregisterControlId(item.ElementId)
        }

        if (item.hasOwnProperty('Elements')) {

          item.Elements.forEach((subitem, subindex) => {
            if (subitem.Id !== undefined) {
              console.log('unregister subchild: ', subitem.Id)
              unregisterControlId(subitem.ElementId)
            }
          })
        }

      })

    }
  }

  function unregisterSectionControls(pSection) {

    // console.log('---> unregister controls: ', pSection.Elements)
    pSection.Elements.forEach((item, index) => {
      // console.log(' --> delete id on: ', item.Id)
      unregisterControlId(item.ElementId)
      if (item.hasOwnProperty('Elements')) {
        item.Elements.forEach((subitem, subindex) => {
          if (subitem.Type !== 'GridRow') {
            // console.log(' ----> delete subid on: ', subitem.Id)
            unregisterControlId(subitem.ElementId)
          }
          if (subitem.hasOwnProperty('Elements')) {
            subitem.Elements.forEach((subsubitem, subsubindex) => {
              // console.log(' ------> delete subsubid on: ', subsubitem.Id)
              unregisterControlId(subsubitem.ElementId)
            })
          }
        })
      }
    })

  }

  function getDefaultID(pType) {
    // console.log(`--->: getDefaultID -> pType: `, pType)
    // console.log('idsCollection: ', idsCollection)

    const idPosition = (idsCollection[pType] === undefined) ? 1 : idsCollection[pType].collection.length + 1
    const positionNumber = idPosition < 10 ? '00' + idPosition : idPosition < 100 ? '0' + idPosition : idPosition

    let prefix
    if (pType.includes('.')) prefix = `${pType.slice(0, 3).toUpperCase()}.${pType.slice(pType.indexOf('.') + 1, pType.indexOf('.') + 4).toUpperCase()}`
    else prefix = `${pType.slice(0, 3).toUpperCase()}`

    let newId = `${prefix}-${positionNumber}`

    // Check for duplicates
    if (Array.from(idsLoadedList).findIndex(item => item === newId) > -1) newId += '-copy'

    // idsCollection[pType].collection.push(newId)

    // setIdList([...idList, newId])
    // validateId(newId)

    return newId
  }


  function renameSectionTitle(pTitle) {
    switch (pTitle) {
      case 'Emailregister':
        return 'Email Register'
      case 'Smsregister':
        return 'SMS Register'
      default:
        break
    }
  }

  /**--------------------------------------------------**
   * @desc - Return building Panel                 -----*
   *----------------------------------------------------*/

  // ---> ENTRY POINT
  function getHTML() {
    if (json.Sections.length > 0) {

      // Browse the json to register every Ids
      json.Sections.forEach(section => {
        // console.log('section elements: ', section.Title, elements)
        if (section.hasOwnProperty('Elements')) {
          section.Elements.forEach((item, index) => {
            // console.log('item: ', item)
            if (item.Id !== undefined) registerControlId(item)
            if (item.hasOwnProperty('Elements')) {
              item.Elements.forEach((subitem, index) => {
                // console.log('subitem: ', subitem)
                if (subitem.Id !== undefined) registerControlId(subitem)
                if (subitem.hasOwnProperty('Elements')) {
                  subitem.Elements.forEach((subsubitem, subsubindex) => {
                    // console.log('subsubitem: ', subsubitem)
                    if (subsubitem.Id !== undefined) registerControlId(subsubitem)
                  })
                }
              })
            }
          })
        }
      })

      // Get controls for the active section
      let currentSectionToRender = json.Sections[currentSectionIndex]
      if (currentSectionToRender.Elements.length > 0) return currentSectionToRender.Elements.map(item => {
        // Build list of Ids at load time
        idsLoadedList.add(item.Id)
        return getControl(item)
      })
    }
  }

  function getControl(pItem) {
    let id = pItem.ElementId
    // registerControlId(pItem)

    if (pItem.DatabaseField === 'ValidFrom') {
      console.log('DatabaseField FROM found here: ', id)
      databaseFieldLocation.from = id
    }
    if (pItem.DatabaseField === 'ValidTo') {
      console.log('DatabaseField TO found here: ', id)
      databaseFieldLocation.to = id
    }
    return <Control
      controlObj={pItem}
      selectedControl={selectedControl}
      onClickHandler={(control) => {
        // Select control from control's child
        if (control.ElementId) onSelectControlHandler(control)
        // Select control
        else onSelectControlHandler(pItem)
      }}
      moveUp={(control, pIsSubControl) => moveUpHandler(control, pIsSubControl)}
      moveDown={(control, pIsSubControl) => moveDownHandler(control, pIsSubControl)}
      delete={(control) => deleteHandler(control)}
      key={id} />
  }

  function onSelectControlHandler(pControl) {
    // console.log(`--->: pControl selected: `, pControl)
    setCurrentKeyword('')
    setSelectedControl(pControl)
  }

  /**-------------------------------------------------**
  * @desc - Build the HTML from JSON              -----*
  *----------------------------------------------------*/
  function onJsonChangeHandler(e) {
    let currentValue = JSON.parse(document.getElementById('json-output').value)
    setJson(currentValue)
  }

  /**-------------------------------------------------**
  * @desc - Build the Panels tabs                 -----*
  *----------------------------------------------------*/
  let sectionNavigation = () => {
    if (json.Sections.length > 0) {
      return json.Sections.map((section, index) => {
        let sectionTitle = (json.Sections[index] !== undefined) ? json.Sections[index].Title : ''
        return <button
          key={index}
          onClick={() => setCurrentSectionIndex(index)}
          className={`tab-section ${currentSectionIndex === index ? 'tab-active' : ''}`}
        >{sectionTitle}</button>
      })
    }
  }

  /**-------------------------------------------------**
  * @desc - Manipulate the JSON                   -----*
  *----------------------------------------------------*/

  function moveUpHandler(pControl, pIsSubControl) {
    const parentElement = getParentElementFromId(pControl.ElementId)
    moveElement(pControl, parentElement, 'moveUp', pIsSubControl)

  }
  function moveDownHandler(pControl, pIsSubControl) {
    const parentElement = getParentElementFromId(pControl.ElementId)
    moveElement(pControl, parentElement, 'moveDown', pIsSubControl)
  }

  function moveElement(pControl, pParentElement, pDirection, pIsSubControl) {
    const container = (!pIsSubControl) ? pParentElement : pParentElement.Elements

    const position = container.findIndex(item => item.ElementId === pControl.ElementId)
    switch (pDirection) {
      case 'moveUp':
        if (position > 0) {
          container.splice(position - 1, 0, container.splice(position, 1)[0])
        }
        break
      case 'moveDown':
        if (position < container.length - 1) {
          container.splice(position + 1, 0, container.splice(position, 1)[0])
        }
        break
      default:
        break
    }
    setJson({ ...json })
  }

  function deleteHandler(pControl) {
    deleteElementFromId(pControl.ElementId)
  }

