  function deleteKeyword(pKeyword) {
    const keywordIndex = selectedControl.Keywords.findIndex(item => item === pKeyword)
    delete selectedControl.Keywords[keywordIndex]
    let jsonToUpdate = { ...json }
    setJson(jsonToUpdate)
  }

  function deleteIdValue(pKeyword) {
    const keywordIndex = selectedControl.AssociatedId.names.findIndex(item => item === pKeyword)
    delete selectedControl.AssociatedId.names[keywordIndex]
    let jsonToUpdate = { ...json }
    setJson(jsonToUpdate)
  }

  function deleteData(pData) {
    const dataIndex = selectedControl.Data.findIndex(item => item === pData)
    console.log('---> selectedControl.OptionedControls: ', selectedControl.OptionedControls)

    const optionIndex = selectedControl.OptionedControls.findIndex(item => item.SelectedString === pData)

    if (dataIndex > -1) selectedControl.Data = selectedControl.Data.filter((item, index) => index !== dataIndex)
    if (optionIndex > -1) selectedControl.OptionedControls = selectedControl.OptionedControls.filter((item, index) => index !== optionIndex)
    let jsonToUpdate = { ...json }
    setJson(jsonToUpdate)
  }

